@import "./variables";

.listed-task-container{
    width: 100%;
    margin: 0 auto;
    border-radius: 10px;
    background-color: white;
    border: 1px solid white;
    padding: 0.5rem 0.7rem;
    transition: border 0.15s;
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    user-select: none;
    animation: fadeIn forwards 0.3s;

    &:hover{
        border: 1px solid $blue_hover;
    }

    // &.completed{
    //     background-color: rgb(216, 248, 216);
    // }

    &.drag{
        display: none;
        cursor: grabbing;
    }

    .listed-task-content{
        display: flex;
        flex-direction: column;
        gap: 0.8rem;
        width: 100%;

        .listed-task-title-and-subtitle{
            display: flex;
            flex-direction: column;
            justify-content: left;

            .listed-task-title{
                font-size: 1.05rem;
                font-weight: 700;
                text-align: left;
            }
    
            .listed-task-subtitle{
                font-size: 0.95rem;
                text-align: left;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
                color: $grey;
                font-weight: 300;
            }
        }

    }

    .listed-task-infos{
        display: flex;
        align-items: center;
        justify-content: space-between;

        .quick-actions{
            display: flex;
            align-items: center;
            gap: 0.3rem;

            .icon-span{

                .icon{
                    animation: fadeIn forwards 0.15s;
    
                    &.green{
                        color: $green;
                    }
                }
                
                &:hover{

                    &.green{
                        color: $green;
                        
                        .icon{
                            color: $green;
                        }
                    }
                    
                    .icon{
                        color: $blue_hover;
                    }
                }
            }

            .options-menu{
                top: unset;
                bottom: 1rem;
                right: 5.5rem;
            }
        }
    }
}

#current-priority{
    gap: 1em;
    font-weight: 600;
}