@import "./variables";

.current-board-info-bar{
    display: flex;
    align-items: center;
    position: relative;
    gap: 2rem;
    margin-bottom: 1rem;

    .search-bar-container{
        align-self: flex-end;
    }

    .search-results{
        position: absolute;
        border: 1px solid $grey;
        padding: 0.5em;
        width: 40%;
        min-height: 100%;
        max-height: 300px;
        overflow-y: auto;
        top: 110%;
        right: 35%;
        background-color: white;
        border-radius: 5px;
        z-index: 100;
        display: flex;
        flex-direction: column;
        gap: 0.4rem;

        .search-result{
            background-color: $light_grey2;
            padding: 0.5em;
            border-radius: 8px;
            display: flex;
            align-items: center;
        }
    }

    .project-title-container{
        display: flex;
        flex-direction: column;

        h3{
            font-weight: 600;
            color: $grey;
            font-size: 1rem;
        }
        
        .project-title{
            display: flex;
            align-items: center;
            gap: 0.5rem;
            padding-inline: 0.5rem;

            .icon{
                scale: 1.7;
                color: $blue;
            }
    
            .icon.check{
                scale: 1.3;
                color: $green;
            }

            .input-container{
                input{
                    font-size: 1.5rem;
                    font-weight: 600;
                    border: none;
                    background-color: transparent;

                    &.title-input{

                        &.active{
                            font-style: italic;
                            color: $grey;
                        }
                    }
                }
            }
        }

    }

    .icon-span.menu{
        border: 1px solid $light_grey;
        border-radius: 5px;
        transition: background-color 0.1s;
        align-self: flex-end;

        .icon{
            color: $blue;
            scale: 1.5;
        }

        &:hover{
            background-color: $blue;

            .icon{
                color: $light;
            }
        }
    }
}

@media screen and (min-width: 200px) and (max-width: 1440px) {
    .current-board-info-bar{
        gap: 1rem;
        .project-title-container{
            width: 25vw;

            .project-title{
                flex: 1;

                .input-container{
                    width: 20vw;

                    input{
                        width: 100%;
                    }
                }
            }
        }
    }
}

@media screen and (min-width: 200px) and (max-width: 1024px) {
    .current-board-info-bar{
        gap: 1rem;
        flex-wrap: wrap;
        justify-content: space-around;

        .search-results{
            width: 60%;
            left: 0;
            
            .search-result{
                justify-content: space-between;
                gap: 1em;

                &>div{
                    h3{
                        word-break: keep-all;
                    }
                }
            }
        }
        
        .project-title-container{
            width: 85vw;

            .project-title{
                flex: 1;

                .input-container{
                    width: 100%;

                    input{
                        width: 100%;
                    }
                }
            }

            .flex1{
                flex: none;
                display: none;
            }
        }

        .icon-span.menu{
            justify-self: flex-end;
        }

        .flex1{
            flex: none;
            display: none;
        }
    }
}

@media screen and (min-width: 951px) and (max-width: 1023px) {
    .current-board-info-bar{

        .options-menu{
            top: 70%;
            right: 7rem;
        }

        .search-results{
            width: 45%;
        }
    }
}

@media screen and (min-width: 451px) and (max-width: 950px) {
    .current-board-info-bar{
        .search-results{
            width: 65%;
        }
    }
}

@media screen and (min-width: 769px) and (max-width: 950px) {
    .options-menu{
        top: 70%;
        right: 5rem;
    }
}

@media screen and (min-width: 700px) and (max-width: 768px) {
    .current-board-info-bar{
        .options-menu{
            top: 70%;
            right: 4rem;
        }
    }
}
@media screen and (min-width: 451px) and (max-width: 700px) {
    .current-board-info-bar{
        .options-menu{
            top: 70%;
            right: 3rem;
        }
    }
}

@media screen and (min-width: 200px) and (max-width: 450px) {
    .current-board-info-bar{

        .options-menu{
            top: 80%;
            right: 2.5rem;
        }

        .search-results{
            width: 95%;
            left: 0.5rem;
            top: 65%;
            
            .search-result{
                justify-content: space-between;
                gap: 1em;
    
                &>div{
                    h3{
                        word-break: keep-all;
                    }
                }
            }
        }
    }
}